.ContainerProjects{
    min-height: 100vh;
    background: #111;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    width: 100%;
    padding: 3%;
    flex-direction: column;
}
.Projects{
    width: 100%;
    position: relative;
    display:flex;
    justify-content:center;
    align-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    margin: auto;}

.Projects .card{
    position: relative;
    background:transparent;
    margin-left: 2%;
    margin-right: 2%;
}
.face.face2 .content{
    display: flex;
    flex-direction: column;
    align-content: flex-end;
}
.Projects .card .face{
    width: 300px;
    height: 200px;
    transition: 0.5s;
}
.content button{
    background:palevioletred;
    border: none;
    width: 128px  ;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 75%;
    height: 32px;
    font-weight: bolder;
    color: #fff
}
.links{
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    padding-top: 10px;
}
.Projects .card .face.face1{
    text-align: center;
    position: relative;
    background:rgb(165, 96, 105);
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    z-index: 1;
    transform: translateY(100px);
}
.Projects .card:hover .face.face1{
    transform: translateY(0);
    background: palevioletred;
    }
.Projects .card .face.face1 .content{
transition: 0.5s;
color: #fff;
}
.Projects .card:hover .face.face1 .content{
opacity: 1;
}
.Projects .card .face.face1 .content i{
font-size: 100px;
}
.Projects .card .face.face2{
  position: relative;  
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
  box-sizing: border-box;
  box-shadow:  0 20px 50px rgba(0,0,0,0.8);
  transform: translateY(-100px);
}
.Projects .card:hover .face.face2{
    transform: translateY(0);
}
.Projects .card .face.face2 .content p{
 margin: 0;
 padding: 0;
 color: #111;
}
.ContainerProjects h1{
    text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 170%;
  color: #fff;
}




